/* CONTACT COMPONENT */

.card-contact {
  width: 70%;
  margin-top: 50px;
  background: #fff;
  color: #000;
  font-family: "Courier New", Courier, monospace, serif;
  place-content: center;
}

.contact-header {
  font-size: 20px;
  border-bottom: 2px solid #000;
}

.card-contact:hover {
  -webkit-box-shadow: 0 4px 15px rgba(153, 153, 153, 0.3);
  box-shadow: 0 4px 15px rgba(153, 153, 153, 0.3);
  -webkit-transition: 0.5s;
  opacity: 1;
  transition: 0.5s;
}

.btn-submit {
  background: #080;
  color: #fff;
  font-size: 18px;
  width: 8rem;
  border-radius: 25px;
}

/* END CONTACT */
